body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-family: "Inter", system-ui, sans-serif;
  background: radial-gradient(circle at top, #020617, #000);
  color: #e5e7eb;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Ensures vertical centering */
  padding: 15px 5%; /* Use percentage for horizontal padding */
  position: sticky;
  top: 0;
  background: rgba(0,0,0,0.8);
  backdrop-filter: blur(10px);
  z-index: 1000;
}


.navbar a {
  margin-left: 20px;
  color: #38bdf8;
  font-weight: 500;
}

.hero {
  text-align: center;
  padding: 120px 20px;
}

.hero h1 {
  font-size: 3.5rem;
}

.button {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 24px;
  border: 1px solid #38bdf8;
  color: #38bdf8;
  border-radius: 6px;
}

section {
  max-width: 1000px;
  margin: auto;
  padding: 80px 20px;
}

.card img {
  width: 100%;
  border-radius: 10px;
  margin-top: 15px;
}

/* Add a media query for mobile devices */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column; /* Stack logo and links */
    padding: 15px;
    gap: 10px;
  }

  .navbar a {
    margin-left: 10px; /* Reduce spacing between links */
    margin-right: 10px;
    font-size: 0.9rem;
  }

  .hero {
    padding: 60px 20px; /* Reduce large desktop padding */
  }

  .hero h1 {
    font-size: 2.2rem; /* Shrink title to prevent overflow */
  }
}
.gallery-grid {
  display: grid;
  /* Automatically fits as many columns as possible (min 250px each) */
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

.gallery-item {
  background: #262626;
  padding: 10px;
  border-radius: 12px;
  text-align: center;
}

.gallery-item img {
  width: 100%;
  height: 200px;
  object-fit: cover; /* Prevents image distortion */
  border-radius: 8px;
}
